@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Trirong:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

@font-face {
  font-family: "ogg";
  src: url("./fonts/Ogg-Roman.otf");
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999999;
}
.nav .inner {
  display: flex;
  gap: 20px;
  font-family: Manrope;
  font-size: 26px;
}
.nav .inner button {
  border: none;
  background: transparent;
}

video {
  pointer-events: none;
}
.blob1css {
  z-index: 0 !important;
}
@media (max-width: 1000px) {
  .blob1css {
    margin-left: -70vw !important;
    margin-top: 60vh !important;
    bottom: none !important;
    right: none !important;
    transform: scale(0.9);
  }
  .blob2css {
    margin-left: 70vw !important;
    margin-top: -50vh !important;
    bottom: none !important;
    right: none !important;
    transform: rotate(55deg) scale(0.9);
  }
}
@media (max-width: 600px) {
  .blob1css {
    margin-left: -70vw !important;
    margin-top: 60vh !important;
    bottom: none !important;
    right: none !important;
    transform: scale(0.8);
  }
  .blob2css {
    margin-left: 70vw !important;
    margin-top: -50vh !important;
    bottom: none !important;
    right: none !important;
    transform: rotate(55deg) scale(0.7);
  }
}
@media (min-width: 1000px) {
  .blob1css {
    margin-left: -72vw !important;
    margin-top: 54vh !important;
    bottom: none !important;
    right: none !important;
  }
  .blob2css {
    margin-left: 76vw !important;
    margin-top: -54vh !important;
    bottom: none !important;
    right: none !important;
    transform: rotate(55deg);
  }
}
.-top-50 {
}
.max-w-600 {
  max-width: 600px;
  width: 600px;
  z-index: -1;
}
.max-h-600 {
  max-height: 600px;
  height: 600px;
  z-index: -1;
}

.pointer-none {
  pointer-events: none;
}
.text-style-vids {
  min-height: 120px;
  border-bottom: 1px solid black;
  width: 50%;
  max-width: 650px;
  min-width: 450px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  padding: 30px 0px;
}
.text-style-vids a {
  z-index: 100;
  color: black;
}

.codepen h3 {
  font-family: "Manrope";
  font-size: 25px;
  font-weight: 300;
}
.codepen a {
  font-family: "IBM Plex Sans" !important;
  word-break: break-all;
  font-weight: 600 !important;
  font-size: 30px !important;
  color: rgb(140, 52, 255);
}

.text-style-vids a {
  font-family: "Manrope";
  font-size: 50px;
  margin-bottom: 10px;
  font-weight: 100;
}
@media (max-width: 648px) {
  .text-style-vids a {
    font-size: 40px;
  }
}
.heading-1 {
  font-family: "ogg";
  font-size: 50px;
  margin-bottom: 10px;
}
.font-manrope {
  font-family: Manrope;
}
.ibm {
  font-family: "IBM Plex Sans";
  font-size: 19px;
}

.intro-text {
  font-size: calc(5vh + 2vw);
  font-family: "ogg";
  margin: 0 2.5rem;
}
@media (min-width: 640px) {
  .intro-text {
    font-size: 7.5vh;
  }
}
@media (min-width: 768px) {
  .intro-text {
    font-size: 8vh;
    margin: 0 5rem;
  }
}
@media (min-width: 1024px) {
  .intro-text {
    font-size: 8.5vh;
    margin: 0vw 10vw;
  }
}
@media (min-width: 1280px) {
  .intro-text {
    font-size: 9vh;
  }
}

@media (max-width: 600px) {
  .text-style-vids {
    min-width: 90%;
  }
  .grid a {
    word-break: break-all;
  }
  .w-full h1 {
    text-align: center;
  }
  .grid h2 {
    text-align: center;
  }
  .grid h4 {
    text-align: center;
  }
}
@media (max-width: 450px) {
  .intro-text {
    margin: 0vw 1.5rem;
  }
}
@media (max-width: 600px) {
  .nav .inner {
    gap: 10px;
    font-size: 22px;
  }
}


.blobby {
  background:red;
  width: calc(300px + 10vw);
  height: calc(300px + 10vw);
  position: absolute;
  filter: blur(calc(40px + 5vw));
  opacity: 0.5;
  z-index: -1;
  border-radius: 50%;
}
.blobby:nth-child(odd) {
  right:0;
}
.bg-card {
  background:rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(20px);
}
:root {
  --color1: rgb(164, 157, 255);
  --color2: rgb(162, 255, 227);
  --color3: rgb(141, 181, 255);
}
.blobby:nth-child(1) {
 background: var(--color1)
}
.blobby:nth-child(2) {
 background: var(--color2)
}
.blobby:nth-child(3) {
 background: var(--color3)
}
.blobby:nth-child(4) {
 background: var(--color1)
}
.blobby:nth-child(5) {
 background: var(--color2)
}
.blobby:nth-child(6) {
 background: var(--color3)
}
.blobby:nth-child(7) {
  background: var(--color1)
 }
 .blobby:nth-child(8) {
  background: var(--color2)
 }
 .blobby:nth-child(9) {
  background: var(--color3)
 }
 .blobby:nth-child(10) {
  background: var(--color1)
 }
 .blobby:nth-child(11) {
  background: var(--color2)
 }
 .blobby:nth-child(12) {
  background: var(--color3)
 }
 .blobby:nth-child(13) {
  background: var(--color1)
 }